@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Raleway', sans-serif;
}

.container-main {
  background-image: url(../assets/background1.png);
  /* background-repeat: no-repeat; */
  background-size: cover;
  /* background-position: center top; */
  width: 100%;
  min-height: 100vh;
  /* height: 100%; */
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.text {
  color: white;
  text-shadow: rgb(188, 182, 182) 0.1em 0.1em 0.2em;
  margin: 25px;
  display: flex;
  justify-content: center;
}

.div-container-conteudo {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  margin-top: 30px;
}

.links-conteudo {
  background-color: rgb(226, 222, 222);
  border-radius: 30px;
  border: rgb(226, 222, 222) solid 2px;
  box-shadow: 6px 6px 4px rgb(206, 203, 203);
  margin: 30px;
}

.links-conteudo:hover {
  background-color: rgba(206, 203, 203, 0.75);
  box-shadow: none;
}

.links-conteudo h2 {
  font-size: 20px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  color: rgb(0, 0, 0);
  text-shadow: rgb(188, 182, 182) 0.1em 0.1em 0.2em;
  text-decoration: none;
}

.image-cards {
  border-radius: 30px;
  width: 300px;
  height: 200px;
  text-decoration: none;
}